.app {  
  padding: 30px;
  height: 100vh;
  
}

.input {
 background-color: rgba(255, 255, 0, 0.623); 
}
.header-todo {
  padding-top: 1rem;  
}

.todo-list {
  background: white;
  border-radius: 4px;
  padding: 5px;
  max-width: 400px;
}

.todo {
  background: #fff;  
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
  padding: 3px 10px;
  font-size: 12px;
  margin-bottom: 6px;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.red-symbol{
color:red;
}
.green-symbol{
color:green;
}